<template>
  <div class="wrapper">
     <div class="hall" v-show="!flag">
       <img src="/img/gost.png" alt="gost" class="gost" @click="showGost">
    </div>
     <img src="/img/face.png" alt="face" class="face" ref='face' v-show="flag">
  </div>
</template>
<script>
export default {
   data() {
     return {
       flag:false,
     }
   },
   methods: {
     showGost () {
       this.flag = true;
       this.$refs.face.classList.add('boo')
       setTimeout(() => {
         this.$router.push('/level2')
       },2000);
     }
   }
}
</script>
<style scoped>
  .wrapper {
     width: 600px;
     margin: 50px auto;
     height: 400px;
     position: relative;
  }
  h1 {
    color:#fff;
  }
  .hall {
    width:100%;
    height: 400px;
    background-image:url('/img/hall.jpg');
    background-repeat: no-repeat;
    background-size:100% 100%;
    position: relative;
    text-align:center;
  }
  .gost {
    position: absolute;
    height: 100px;
    top:0;
    left:240px;
    top:200px;
    cursor:pointer;
  
  }
  .face {
    margin:0 auto;
    width: 100px;
    height:100px;
    bottom:0;
    position: absolute;
    left:50%;
    margin-left:-50px;
  }
  .boo {
    -webkit-transform: scale(9);
    -ms-transform: scale(9);
    transform: scale(9);
  } 
</style>


